
import * as z from 'zod';

export const createAppointmentFormSchema = z.object({
  patient_id: z.string().min(1, { message: '' }),
  date: z.string().min(1, { message: '' }),
  time: z.string().min(1, { message: '' }),
  purpose: z.string().min(1, { message: '' }),
  notes: z.string().optional(),
});

