
import { z } from 'zod';

export const batchAppointmentFormSchema = z.object({
  date: z.string().min(1, { message: '' }),
  startTime: z.string().min(1, { message: '' }),
  duration: z.number().min(5, { message: ' 5 ' }).max(60, { message: ' 60 ' }).default(15),
  purpose: z.string().min(1, { message: '' }),
  notes: z.string().optional(),
});

export type BatchAppointmentFormValues = z.infer<typeof batchAppointmentFormSchema>;

export interface TimeSlot {
  patientId: string;
  time: string;
}
