import React, { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import {
  Card,
  CardContent,
  CardHeader,
  CardTitle,
  CardDescription,
  CardFooter,
} from "@/components/ui/card";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Patient } from "@/integrations/supabase/schema";

interface PatientFormProps {
  patient?: Patient;
  onSubmit: (patient: Partial<Patient>) => void;
  onCancel: () => void;
}

const PatientForm: React.FC<PatientFormProps> = ({
  patient,
  onSubmit,
  onCancel,
}) => {
  const [formData, setFormData] = useState<Partial<Patient>>(
    patient || {
      name: "",
      phone: "",
      line_id: "",
      address: "",
      ID_card: "",
      gender: "",
      birth_date: "",
      distance_from_hospital: null,
    }
  );

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSelectChange = (name: string, value: string) => {
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onSubmit(formData);
  };

  return (
    <Card className="w-full max-w-2xl mx-auto bg-white shadow-lg animate-scale-in">
      <CardHeader>
        <CardTitle className="text-xl text-pharmacy-800">
          {patient ? "" : ""}
        </CardTitle>
        <CardDescription>
          
        </CardDescription>
      </CardHeader>

      <form onSubmit={handleSubmit}>
        <CardContent className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="name">-</Label>
              <Input
                id="name"
                name="name"
                value={formData.name || ""}
                onChange={handleChange}
                placeholder="-"
                required
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="ID_card"></Label>
              <Input
                id="ID_card"
                name="ID_card"
                value={formData.ID_card || ""}
                onChange={handleChange}
                placeholder=" 13 "
                maxLength={13}
                pattern="[0-9]{13}"
                required
              />
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="phone"></Label>
            <Input
              id="phone"
              name="phone"
              value={formData.phone || ""}
              onChange={handleChange}
              placeholder=""
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="line_id">LINE ID ()</Label>
            <Input
              id="line_id"
              name="line_id"
              value={formData.line_id || ""}
              onChange={handleChange}
              placeholder="LINE ID"
            />
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="gender"></Label>
              <Select
                value={formData.gender || ""}
                onValueChange={(value) => handleSelectChange("gender", value)}
              >
                <SelectTrigger>
                  <SelectValue placeholder="" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value=""></SelectItem>
                  <SelectItem value=""></SelectItem>
                  <SelectItem value=""></SelectItem>
                </SelectContent>
              </Select>
            </div>

            <div className="space-y-2">
              <Label htmlFor="birth_date"></Label>
              <Input
                type="date"
                id="birth_date"
                name="birth_date"
                value={formData.birth_date || ""}
                onChange={handleChange}
              />
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="address"></Label>
            <Textarea
              id="address"
              name="address"
              value={formData.address || ""}
              onChange={handleChange}
              placeholder=""
              rows={3}
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="distance_from_hospital"> ()</Label>
            <Input
              type="number"
              step="0.1"
              min="0"
              id="distance_from_hospital"
              name="distance_from_hospital"
              value={formData.distance_from_hospital?.toString() || ""}
              onChange={handleChange}
              placeholder=" 5.2"
            />
          </div>
        </CardContent>

        <CardFooter className="flex justify-end space-x-2">
          <Button type="button" variant="outline" onClick={onCancel}>
            
          </Button>
          <Button
            type="submit"
            className="bg-pharmacy-600 hover:bg-pharmacy-700 text-white"
          >
            {patient ? "" : ""}
          </Button>
        </CardFooter>
      </form>
    </Card>
  );
};

export default PatientForm;
